@import url(https://fonts.googleapis.com/css?family=Kalam|Marck+Script|Playfair+Display|Source+Sans+Pro&display=swap);
.table{display:grid;grid-template-columns:repeat(10, 1fr);grid-template-rows:repeat(10, 1fr);align-items:center;height:75vh;width:calc(100% - 60px);text-align:center;margin-top:10px;padding:30px}.table div{border:1px solid black;height:100%;display:flex;align-items:center;justify-content:center;font-size:32px}@media print{form,button{display:none}}.font-1{font-family:'Kalam', cursive}.font-2{font-family:'Marck Script', cursive}.font-3{font-family:'Source Sans Pro', sans-serif}.font-4{font-family:'Playfair Display', serif}fieldset{padding:0;margin:0;border:none}fieldset label{margin:0 10px;line-height:30px}fieldset label input{margin-right:5px}

