@import url('https://fonts.googleapis.com/css?family=Kalam|Marck+Script|Playfair+Display|Source+Sans+Pro&display=swap');

.font-1 {
  font-family: 'Kalam', cursive;
}

.font-2 {
  font-family: 'Marck Script', cursive;
}

.font-3 {
  font-family: 'Source Sans Pro', sans-serif;
}

.font-4 {
  font-family: 'Playfair Display', serif;
}

fieldset {
  padding: 0;
  margin: 0;
  border: none;
  label {
    margin: 0 10px;
    line-height: 30px;
    input {
      margin-right: 5px;
    }
  }
}
